import React from 'react';
import {createUseStyles} from 'react-jss'
import Slider from "react-slick";
import Dialog from '@material-ui/core/Dialog';

const useStyles = createUseStyles({
    container: {
        color: 'white',
        background: '#202050',
        borderRadius: 5,
        padding: 40,
        fontSize: 30,
        fontFamily: 'Russo One',
        margin: '6px',
    },
    slideBody: {
        height: 'auto',
        textAlign: 'center',
    },
    title: {
        fontWeight: 'bold',
        textDecoration: 'none',
    },
    link: {
        color: 'white',
        textDecoration: 'none',
        '&:hover': {
            color: 'cyan !important',
        }
    },
    slideImage: {
        width: 'auto',
        height: '640px',
        display: 'inherit !important',
        margin: 'auto'
    }
});

export const ImageDialog = ({children, isOpen, onClose, initialSlideId}) => {
    const classes = useStyles();
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        draggable: false,
        fade: false,
        autoplay: false,
        autoplaySpeed: 5000,
        initialSlide: initialSlideId
    };

    return (
        <Dialog open={isOpen} onClose={onClose}fullWidth={true} maxWidth='lg'>
            <div className={classes.container}>
                <Slider {...settings}>
                    {children}
                </Slider>
            </div>
        </Dialog>
    );
}
